<template>
    <div>
        <el-card style="margin-bottom: 10px">
            <div >
                <el-dropdown @command="handleCommand">
                    <el-button type="primary">
                        {{buttonName}}<i class="el-icon-arrow-down el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="所有商品">所有商品</el-dropdown-item>
                        <el-dropdown-item command="先租后买">先租后买</el-dropdown-item>
                        <el-dropdown-item command="以租代售">以租代售</el-dropdown-item>
                        <el-dropdown-item command="共享租赁">共享租赁</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <el-input class="goodsHeader"
                        style="width:500px"
                        placeholder="请输入内容"
                        v-model="search"
                        clearable>
                </el-input>
                <el-button type="primary" @click="searchGoods">搜索</el-button>
                <el-button type="warning" icon="el-icon-shopping-cart-full" @click="checkCart">查看租赁购物车</el-button>
                <el-button type="success" icon="el-icon-s-order" @click="checkOrder">查看订单</el-button>
                <el-tag type="info" style="margin-left: 30px">通知：目前暂时不支持查看商品详情</el-tag>
            </div>

        </el-card>
        <el-row :gutter="13">
            <el-col :span="6" v-for="(goods,index) in goodsList" :key="goods.goodId">
                <el-card shadow="hover" class="mgb20" style="height:450px;width:300px;" >
                    <div class="user-info">
                        <img src="../../assets/img/book.jpg" class="user-avator" alt />
                    </div>
                    <div class="user-info-list red">
                        <span>￥{{goods.goodPrice}}</span>
                        <span style="margin-left: 10px;margin-bottom: 5px">
                            <el-tag v-if="goods.goodStatus == 1" >先租后买</el-tag>
                            <el-tag v-else-if="goods.goodStatus == 2" type="success">以租代售</el-tag>
                            <el-tag v-else type="warning">共享租赁</el-tag>
                        </span>
                    </div>
                    <div class="user-info-list normal">
                        <span>{{goods.goodName}}</span>
                    </div>
                    <div class="user-info-list">
                        <i class="el-icon-s-shop"></i>
                        <span>{{goods.goodOwner}}</span>
                    </div>
                    <div class="user-info-list" style="float: right">
                        <el-button type='danger' icon="el-icon-shopping-cart-2" @click="addToCart(goods)">加入购物车</el-button>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <!--购物车-->
        <el-dialog
                title="购物车"
                :visible.sync="dialogVisible"
                width="50%">
            <el-table :data="gridData" height="300">
                <el-table-column property="goodNum" label="商品编号"></el-table-column>
                <el-table-column property="goodName" label="商品名称"></el-table-column>
                <el-table-column property="goodPrice" label="单价"></el-table-column>
                <el-table-column property="goodOwner" label="租方"></el-table-column>
                <el-table-column property="goodStatus" label="租赁方式"></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                type="danger"
                                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="rentForSure">确 定 租 赁</el-button>
          </span>
        </el-dialog>
        <!--订单-->
        <el-dialog
                title="订单预览"
                :visible.sync="dialogOrderVisible"
                width="50%">
            <div>订单编号：{{uuid}}</div>
            <el-table :data="gridOrderData" height="300">
                <el-table-column property="goodNum" label="商品编号"></el-table-column>
                <el-table-column property="goodName" label="商品名称"></el-table-column>
                <el-table-column property="goodPrice" label="单价"></el-table-column>
                <el-table-column property="goodOwner" label="租方"></el-table-column>
                <el-table-column property="goodStatus" label="租赁方式"></el-table-column>
            </el-table>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogOrderVisible = false">取 消</el-button>
                <el-button type="primary" @click="insertOrder">确 定 下 单</el-button>
          </span>
        </el-dialog>
    </div>

</template>

<script>
export default {
    name:'rent',
    data(){
        return{
            // 商品展示
            goodsList:[],
            // 搜索内容
            search:'',
            // 按钮内容
            buttonName:'所有商品',
            dialogVisible:false,
            dialogOrderVisible:false,
            gridData: [],
            gridOrderData:[],
            uuid:''
        }
    },
    created() {
        this.getAllGood()
    },
    methods:{
        getAllGood(goodName){
            if(goodName == undefined){
                goodName = ''
            }
            this.$http.get('/good/findGoods?goodName='+goodName)
                .then((data)=>{
                    console.log(data);
                    if(data.status == '200'){
                        for(let i = 0 ;i <data.data.length ;i++){
                            if(data.data[i].goodStatus >0){
                                this.goodsList.push(data.data[i])
                            }
                        }

                    }else{
                        this.$message.error('出错了！');
                    }

                })
                .catch((err)=>{
                    console.log(err);
                })
        },
        handleCommand(command){
            this.buttonName = command
        },
        searchGoods(){

            this.goodsList = []
            let status;
            if(this.buttonName == '所有商品'){
                this.getAllGood(this.search)
                return
            }
            if(this.buttonName == '先租后买'){
                status = 1
            }else if(this.buttonName == '以租代售'){
                status = 2
            }else{
                status = 3
            }
            if(status != 0 && this.search== ''){
                this.$http.get('good/findGoodsByStatus?status='+status)
                    .then((data)=>{
                        if(data.status == '200'){
                            for(let i = 0 ;i <data.data.length ;i++){
                                if(data.data[i].goodStatus <=0){
                                    data.data.splice(i,1)
                                }
                            }
                            this.goodsList = data.data
                        }else{
                            this.$message.error('出错了！');
                        }
                    })
                    .catch(err=>console.log(err))
            }else if(status != 0 && this.search!= ''){
                this.$http.get('/good/findGoodsByStatusAndName?status='+status + "&goodName="+this.search.trim())
                    .then((data)=>{
                        if(data.status == '200'){
                            for(let i = 0 ;i <data.data.length ;i++){
                                if(data.data[i].goodStatus <=0){
                                    data.data.splice(i,1)
                                }
                            }
                            this.goodsList = data.data
                        }else{
                            this.$message.error('出错了！');
                        }
                    })
                    .catch(err=>console.log(err))
            }

        },
        getUUID() {
            return (((1+Math.random())*0x10000)|0).toString(16).substring(1);
        },
        checkCart(){
            this.dialogVisible = true;
        },
        addToCart(goods){
            let newGoods = JSON.parse(JSON.stringify(goods))
            if(newGoods.goodStatus == 1){
                newGoods.goodStatus = '先租后买'
            }else if(newGoods.goodStatus == 2){
                newGoods.goodStatus = '以租代买'
            }else{
                newGoods.goodStatus = '共享租赁'
            }
            this.gridData.push(newGoods);
            this.$message.success("加入购物车成功！");
        },
        handleDelete(index, row){
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                for (var i = 0; i < this.gridData.length; i++) {
                    if (this.gridData[i].goodId == row.goodId) {
                        this.gridData.splice(i, 1);
                    }
                }
            })
            .catch((err)=>console.log(err))
            console.log(this.gridData);
        },
        rentForSure(){
            this.gridOrderData = this.gridData;
            this.gridData = []
            if(this.uuid== ''){
                let uuid = (this.getUUID()+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+this.getUUID()+this.getUUID());
                this.uuid = uuid
            }
            this.dialogOrderVisible = true
            this.dialogVisible = false
        },
        checkOrder(){
            if(this.uuid== ''){
                let uuid = (this.getUUID()+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+"-"+this.getUUID()+this.getUUID()+this.getUUID());
                this.uuid = uuid
            }
            this.dialogOrderVisible = true;
        },
        insertOrder(){
            this.gridData = []
            let goods = ""
            let money = 0
            this.gridOrderData.forEach(item=>{
                goods += "," + item.goodNum
                money += item.goodPrice
                console.log(item.goodPrice);
            })
            console.log(this.gridOrderData);
            let obj={

            }
            let userId = localStorage.getItem('userId')
            let data = new Date()
            let year = data.getFullYear()
            let month = data.getMonth() +1
            let day = data.getDate()
            let goodDate = year + "-" + month + "-" + day

            obj.goods = goods
            obj.userId = userId
            obj.orderTime = goodDate
            obj.orderPrice = money
            obj.orderNum = this.uuid
            console.log(obj.orderTime);
            this.$http.post('/orderList/insertOrder',obj)
                .then((data)=>{
                    if(data.status == '200'){
                        this.dialogOrderVisible = false
                        this.gridData= []
                        this.gridOrderData = []
                        this.$message.success('成功生成订单！')
                    }
                    console.log(data);
                })
                .catch(err=>console.log(err))
            this.dialogOrderVisible = false
        }
    }
};
</script>

<style scoped>

    .user-info {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-bottom: 20px;
    }

    .user-avator {
        width: 100%;
        height: 250px;
    }

    .user-info-cont div:first-child {
        font-size: 30px;
        color: #222;
    }

    .user-info-list {
        font-size: 14px;
        color: #999;
        line-height: 25px;
    }
    .red {
        color:red;
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 5px;
    }
    .normal {
        color:#3d3d3d;
        height:45px;
        margin-bottom: 10px;
        overflow: hidden;
    }
    .mgb20 {
        margin-bottom: 20px;
    }
    .goodsHeader {
        margin: 0 10px;
    }
</style>